.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f1f1f1;
}

.logo {
    width: 340px;
    max-width: 56%;
    margin-bottom: 20px;
    animation: flipTitle 1s;
}

@keyframes flipTitle {
    from {
        transform: rotateX(90deg);
    }

    to {
        transform: rotateX(0deg);
    }
}

.container-input {
    position: relative;
    width: 50%;
    padding: 14px;
    margin-bottom: 20px;
    display: flex;
    border-radius: 2px;
    box-shadow: 1px 2px 2px rgba(0, 0, 0, .8);
    background-color: #fff;
}

.container-input input {
    border: 0;
    font-size: 1.2rem;
    outline: none;
    background-color: transparent;
}

.buttonSearch {
    right: 10px;
    border: 0;
    cursor: pointer;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform .5s;
    background-color: transparent;
}

.buttonSearch:hover {
    color: #ff0000;
    transform: scale(1.2);
}

.buttonSearch:focus {
    color: #ff0000;
    outline: none;
}

.main {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #fff;
    width: 50%;
    padding: 14px;
    border-radius: 4px;
    box-shadow: 1px 2px 2px rgba(0, 0, 0, .8);
}

.main h2 {
    color: #ff0000;
    text-align: center;
    font-size: 2.2rem;
    margin: 15px 0px;
}

.infos {
    display: flex;
    justify-content: space-around;
}

.label {
    font-weight: bold;
    font-size: .8rem;
    margin-bottom: 5px;
}

.main span {
    margin-bottom: 16px;
}

.complemento {
    text-transform: capitalize;
}

@media (max-width: 800px) {
    .infos {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
    }
}